import axios from 'axios'
import con from './config'


const state = {
       links: [],
       sublinks: [],
       personnel: [],
       group: [],
       basepath: window.location.origin,
       basepath1: 'http://localhost/',
       sessionState: "",
}

const getters = {
       allLinks: state => state.links,
       allSubLinks: state => state.sublinks,
       allPersonnel: state => state.personnel,
       allGroup: state => state.group,
       globalBasepath: state => state.basepath1,
       getsession: state => state.sessionState,
}
const actions = {
       async getIISToken({ dispatch, commit }, t) {
              sessionStorage.setItem('origToken', t.token);
              sessionStorage.setItem('origTokenID', t.id);
              await axios.get(`https://iis.emb.gov.ph/embis/pbsapi/?token=${t.token}&token_id=${t.id}`, {
              }).then(function (res) {
                     // check to swms user account and pull the responsibility links
                     sessionStorage.setItem('origin', 'iis');
                     sessionStorage.setItem('cregion', res.data['region']);
                     //dispatch('loginAPI', { tokens: res.data['employee_id'] })
                     con.post("/auth/login", {
                            iis_employee_number: res.data['employee_id'],
                            origin: 0
                     }).then(function (log) {
                            if (log.data.user != null) {
                                   if (log.data.user['region'] == "19") {
                                          sessionStorage.setItem('user_type', 'nswmc');
                                   } else if (log.data.user['region'] == "18") {
                                          sessionStorage.setItem('user_type', 'central');
                                   } else {
                                          sessionStorage.setItem('user_type', 'region');
                                   }

                                   sessionStorage.setItem('region_id', log.data.user['region']);
                                   sessionStorage.setItem('ascwsktken', log.data.access_token);
                                   sessionStorage.setItem('access', log.data.user['user_access_level']);
                                   sessionStorage.setItem('iis_employee_number', log.data.user['id']);
                                   sessionStorage.setItem('name', log.data.user['name']);
                                   sessionStorage.setItem('group', log.data.user['group']);
                                   sessionStorage.setItem('admin', log.data.user['admin']);
                                   sessionStorage.setItem('super_admin', log.data.user['super_admin']);
                                   // window.location.href = "/";

                            } else {
                                   alert('User is not register or deactivated, please contact administrator')
                                   window.location.href = "https://iis.emb.gov.ph/embis/";
                            }

                     }).catch(error => {
                            console.log(error.response.data.error)
                            if (error.response.data.error == "Expired token") {
                                   sessionStorage.clear();
                                   window.location.href = "https://iis.emb.gov.ph/embis/";
                            }
                     })

              }).catch(error => {

              })

       },
       // http://localhost:8080/?token_id=4&token=2524&origin=crs#/
       // http://swm.emb.gov.ph/swm/?token_id=4&token=2524&origin=crs#/
       async getCRSToken({ }, t) {
              localStorage.setItem("origToken", t.user_id);
              localStorage.setItem("origTokenID", t.company_id);
              localStorage.setItem('origin', 'crs');
              await axios.get(`https://iis.emb.gov.ph/embis/api/Crs_Api/json_company_swm?api_key=x&company_id=${t.company_id}&user_id=${t.user_id}`, {

              }).then(function (res) {
                     sessionStorage.setItem('origin', 'crs');
                     sessionStorage.setItem('cregion', res.data['region_name']);
                     sessionStorage.setItem('province_id', res.data['province_id']);
                     sessionStorage.setItem('city_id', res.data['city_id']);

                     //sessionStorage.setItem('name', res.data['userinfo']['first_name'] + " " + res.data['userinfo']['last_name']);
                     sessionStorage.setItem('user_type', 'lgu');
                     con.post("/auth/login", {
                            iis_employee_number: res.data['userinfo']['client_id'],
                            origin: 1
                     }).then(function (log) {
                            if (log.data.user != null) {
                                   sessionStorage.setItem('region_id', log.data.user['region']);
                                   sessionStorage.setItem('ascwsktken', log.data.access_token);
                                   sessionStorage.setItem('access', log.data.user['user_access_level']);
                                   sessionStorage.setItem('iis_employee_number', log.data.user['id']);
                                   sessionStorage.setItem('name', log.data.user['name']);
                                   sessionStorage.setItem('admin', log.data.user['admin']);
                                   window.location.href = "/"; // for development
                            } else {
                                   alert('User is not register or deactivated, please contact administrator')
                                   window.location.href = "https://client.emb.gov.ph/crs/";
                            }
                     })
                            .catch(error => {
                                   console.log(error.response.data.error)
                                   window.location.href = "https://client.emb.gov.ph/crs/"
                                   if (error.response.data.error == "Expired token") {
                                          sessionStorage.clear();
                                          window.location.href = "https://client.emb.gov.ph/crs/";
                                   }
                            })
              }).catch(error => {

              })



       },

       async loginAPI({ commit }, { tokens }) {

              await con.post(`/auth/login`, {
                     iis_employee_number: tokens,
                     origin: 0
              }).then(function (log) {
                     console.log(log)
                     if (log.data.user != null) {
                            if (log.data.user['region'] == "19") {
                                   sessionStorage.setItem('user_type', 'nswmc');
                            } else if (log.data.user['region'] == "18") {
                                   sessionStorage.setItem('user_type', 'central');
                            } else {
                                   sessionStorage.setItem('user_type', 'region');
                            }

                            sessionStorage.setItem('region_id', log.data.user['region']);
                            sessionStorage.setItem('ascwsktken', log.data.access_token);
                            sessionStorage.setItem('access', log.data.user['user_access_level']);
                            sessionStorage.setItem('iis_employee_number', log.data.user['id']);
                            sessionStorage.setItem('name', log.data.user['name']);
                            sessionStorage.setItem('group', log.data.user['group']);
                            sessionStorage.setItem('admin', log.data.user['admin']);
                            sessionStorage.setItem('super_admin', log.data.user['super_admin']);
                            sessionStorage.setItem('login', 1);
                            // window.location.href = "/";

                     } else {
                            alert('User is not register or deactivated, please contact administrator')
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }

              }).catch(error => {
                     console.log(error.response.data.error)
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })


       },
       async getBackendSession({ commit }) {
              await con.get(`/GetBackendSession`).then(function (res) {
                     console.log(res)
              }).catch(error => {
                     console.log(error);
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })
       },



       async getSidebar({ commit }, res) {
              await con.get(`/GetResLink/${res.access_level}`).then(function (res) {
                     commit('allLinks', res.data['Res']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })
       },

       async getSubSidebar({ commit }, res) {
              await con.get(`/GetSubResLink/${res.access_level}`).then(function (res) {
                     commit('allSubLinks', res.data['Res']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })


       },

       async fetchPersonnel({ commit }, res) {
              await con.get(`/GetPersonnel/${res.reg_id}/${res.user_role}/${res.action}/${res.user_id}`).then(function (res) {

                     commit('allPersonnel', res.data['data']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })
       },

       async fetchSpecificPersonel({ commit }, res) {
              let new_data = []
              new_data.push(res)
              commit('allPersonnel', new_data);
              // await con.get(`/GetPersonnel/${res.reg_id}/${res.user_role}/${res.action}/${res.user_id}`).then(function (res) {
              //        commit('allPersonnel', res.data['data']);
              // }).catch(error => {
              //        if (error.response.data.error == "Expired token") {
              //               sessionStorage.clear();
              //               window.location.href = "https://iis.emb.gov.ph/embis/";
              //        }
              // })
       },

       async fetchGroup({ commit }, res) {
              await con.get(`/GetGroup/${res.reg_id}`).then(function (res) {
                     commit('allGroup', res.data['data']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })


       },


};

const mutations = {
       allLinks: (state, User) => (state.links = User),
       allSubLinks: (state, User) => (state.sublinks = User),
       allPersonnel: (state, User) => (state.personnel = User),
       allGroup: (state, data) => (state.group = data),
       setSessionMutation: (state, data) => (state.sessionState = data),
}

export default {
       state,
       getters,
       actions,
       mutations
};